#promo {
  align-items: center;
  background-color: $black;
  display: flex;
  height: 46px;
  justify-content: center;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  width: 100%;

  @include break-min($medium1) {
    height: 58px;
  }

  h1 {
    @include promo-text($white);
  }
}
