.megamenu {
  position: absolute;
  display: none;
  top: 138px;
  left: 0;
  width: 100%;
  z-index: $zMax;

  &__container {
    background-color: $white;
    margin: 0 16.25%;
    display: flex;
  }

  &__left {
    background-color: $off-white;
    display: flex;
    flex: 1 1 551px;
    padding: 45px 12.885662431941924% 55px 7.6225045372050815%;

    img {

    }

    ul.left__links {
      display: flex;
      flex-direction: column;
      margin-left: 10.88929219600726%;

      li  {
          padding: 0;
        a {
          @include menu-links($black);
          text-decoration: none;
        }
      }
    }
  }

  &__right {
    background-color: $white;
    flex: 1 1 421px;
    padding: 45px 6.65083135391924% 55px;
    display: flex;

    ul.right__links {
      display: flex;
      flex-direction: column;

      &:first-child {
        margin-right: 15.201900237529692%;
      }

      li.title {
        @include menu-title($black);
        margin-bottom: 14px;
      }

      ul.links {
        li {
          padding: 0;
          a {
            @include menu-links($black);
            text-decoration: none;
          }
        }
      }
    }
  }
}
