header {
  background: $green;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 64px;
  padding: 0 5.333333333333334%;
  position: relative;
  width: 100%;

  @include break-min($large1) {
    height: 138px;
    padding: 0 3.3333333333333335%;
  }

  .nav__left {
    display: flex;
    align-items: center;

    .nav__hamburger {
      margin-right: 25px;
      cursor: pointer;

      @include break-min($large1) {
        display: none;
      }
    }

    .nav__logo {
      svg {
        width: 56px;

        @include break-min($large1) {
          width: 93px;
        }
      }
    }
  }



ul.nav__center {
  display: none;

  @include break-min($large1) {
    display: flex;
    justify-content: space-between;
    padding: 0 21.949404761904763%;

    li {
      flex: 0 1 auto;
      padding: 0 5.20282%;

        &:first-child {
          padding-left: 0;
        }

        &:last-child {
          padding-right: 0;
        }

        a {
          @include main-nav($white);

          &:hover, &:focus {
            border-bottom: 2px solid $white;
            padding-bottom: 8px;
          }
        }

        &:hover {
          > .megamenu {
            display: block;
          }
        }
      }
    }
  }

  ul.nav__right {
    display: inline-flex;
    li {
      margin-right: 20px;

      @include break-min($large1) {
        margin-right: 30px;
      }

      &:last-child {
        margin-right: 0;
      }
      a {
        svg {
          width: 20px;
          height: 20px;
        }
      }
    }
  }

  .search__container {
    position: absolute;
    background-color: $white;
    display: none;
    left: 5.333333333333334%;
    top: 64px;
    width: 89.33333333333333%;
    z-index: $zMax;

    @include break-min($large1) {
      max-width: 278px;
      left: unset;
      right: 9%;
      top: unset;
    }

    &.search__container--show {
      display: block;
    }

    form {
      display: flex;
      flex-direction: row;
      position: relative;
      align-items: center;

      input {
        border: 1px solid $black;
        height: 45px;
        margin: 5px;
        width: 100%;

        &:focus, &:active, &:focus-within, &:focus-visible {
          border: 1px solid $black;
          border-radius: 0;
          outline-color: $black;
        }

        &::placeholder {
          @include lato('light');
          font-size: rem(16px);
          line-height: lh(45px, 16px);
        }

        @include break-min($large1) {
          height: 36px;
          margin: 0;
        }
      }

      button.btn {
        position: absolute;
        height: 45px;
        min-width: unset;
        right: 5px;
        top: 5px;
        width: 56px;

        @include break-min($large1) {
          height: 36px;
          right: 0;
          top: 0;
        }
      }
    }
  }
}
