.btn {
  @include button-text($white);
  background-color: $black;
  border: 2px solid $black;
  cursor: pointer;
  height: 40px;
  min-width: 124px;
  width: 100%;

  @include break-min($medium1) {
    height: 54px;
  }

  &.btn--secondary {
    @include button-text($white);
    background-color: $green;
    border-color: $white;
    max-width: 140px;
  }

  &.btn--small {
    max-width: 124px;
  }
}
