.newsletter {
  background-color: $green;
  padding: 14px 5.333333333333334% 25px;
  text-align: center;
  width: 100%;

  @include break-min($medium1) {
    padding: 75px 3.3333333333333335% 74px;
  }

  h3 {
    @include h2($white);
    margin-bottom: 11px;

    svg {
      height: 19px;
      width: 28px;

      @include break-min($medium1) {
        height: 36px;
        width: 56px;
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;

    @include break-min($medium1) {
      flex-direction: row;
    }

    p {
      @include p1($white);
      margin-bottom: 56px;
      text-align: center;

      @include break-min($medium1) {
        flex: 1 1 48.847583643122675%;
        text-align: left;
        padding-right: 3.3457249070631967%;
      }
    }

    form {
      @include break-min($medium1) {
        flex: 1 1 47.806691449814124%;
        display: flex;
        justify-content: space-between;
      }

      input {
        display: none;
        @include break-min($medium1) {
          display: flex;
          width: 76.20528771384136%;
          margin-right: 2.021772939346812%;
        }
      }

      button {
        @include break-min($medium1) {
          width: 21.77293934681182%;
        }
      }
    }
  }
}
