footer {
  background-color: $black;
  position: relative;
  width:100%;
  padding: 13px 5.333333333333334% 29px;

  @include break-min($medium1) {
    padding: 62px 3.3333333333333335%;
    display: flex;
    justify-content: space-between;
  }

  .footer__nav {
    ul {
      @include footer-title($white);

      li.state {
        & ~ ul {
          display: none;
        }

        > .icon--close {
          display: none;
        }

        &.state--open {
          & ~ ul {
            display: block;
          }

          > .icon--open {
            display: none;
          }

          > .icon--close {
            display: block;
          }
        }
      }

      @include break-min($medium1) {
        margin-right: 90px;
        margin-top: 0;

        &:last-child {
          margin-right: 0;
        }
      }

      li {
        cursor: pointer;
        position: relative;
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-bottom: 11px;

        @include break-min($medium1) {
          display: flex;
          flex-direction: row;

          &.state, &.state--open {
            & > span.icon--open {
              display: none !important;
            }

            & > span.icon--close  {
              display: none !important;
            }

            & ~ ul {
              display: block;
            }
          }
        }
      }
      ul {
        @include footer-link($white);
        li {
          width: fit-content;

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }

    @include break-min($medium1) {
      display: flex;
      flex-direction: row;
    }
  }

  .footer__social {
    h3 {
      @include footer-title($white);
    }
    ul {
      display: inline-flex;
      li {
        padding-right: 32px;
        &:last-child {
          padding-right: 0;
        }
        svg {
          width: 26px;
          height: 26px;
        }
      }
    }
  }
}
