.carousel__hero {

  .swiper-wrapper {

    .swiper-slide {
      position: relative;
      & > img {
        width: 100%;
        display: block;
        height: auto;
      }

      .slide__content {
        background-color: $cream;
        padding: 16px 13.333333333333334% 34px;
        text-align: center;

        @include break-min($large1) {
          background-color: transparent;
          left: 9.23611111111111%;
          position: absolute;
          padding: 0;
          text-align: left;
          top: 30%;
        }

        h2 {
          @include h1($green);

          @include break-min($large1) {
            margin-bottom: 9px;

            &.text-white--desktop {
              color: $white;
            }
          }
        }

        p {
          @include p1($green);
          margin-bottom: 14px;

          @include break-min($large1) {
            margin-bottom: 26px;

            &.text-white--desktop {
              color: $white;
            }
          }
        }
      }
    }
  }

  .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: 180px;

    @include break-min($medium1) {
      bottom: 270px;
    }

    @include break-min($large1) {
      bottom: 20px;
    }
  }

  .swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
    background-color: $white;
    height: 8px;
    margin-left: 4px;
    margin-right: 4px;
    width: 8px;

    @include break-min($large1) {
      height: 20px;
      margin-left: 10px;
      margin-right: 10px;
      width: 20px;
    }
  }

  .swiper-pagination-bullet {
    opacity: 0.6;

    &.swiper-pagination-bullet-active {
      opacity: 1;
    }
  }
}
