html {
  font-size: $default-font-size;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul, ol {
  list-style: none;
  padding-left: 0;
}

a {
  text-decoration: none;
}

.no-scroll {
  overflow: hidden;
}
