/*------ Media Query Variables (em)
 ----------------------------*/

 //SMALL
 $small1: 20; //320px *

 //MEDIUM
 $medium1: 48; //768px *

 //LARGE
 $large1: 64; //1024px *

 //HUGE
 $huge1: 90; //1440px*

 //Mobile First
 @mixin break-min($media) {
   @if (type-of($media) == 'number') {
     @if (unit($media) == 'px') {
       @media screen and (min-width: #{$media}) {
         @content;
       }
       } @else {
         @media screen and (min-width: #{$media}em) {
           @content;
       }
     }
   } @else {
     @media screen and (#{$media}) {
       @content;
     }
   }
 }

/*------ Unit mixins
 ----------------------------*/

 //base 10 for easier rem calcs
 $default-font-size: 10px;

//Convert px to break
@function rem($px, $base: $default-font-size) {
  @return ($px / $base) * 1rem;
}

//Get raw value
@function get-value($n) {
  @return $n / ($n * 0 + 1);
}

//convert lineheight px to unitless
@function lh($line-height, $font-size) {
  @return get-value($line-height / $font-size);
}

/*------ Utility mixins
----------------------------*/
@mixin visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
}
