.carousel__product {
  background-color: $white;
  padding: 0 5.333333333333334% 30px;
  h3 {
    @include h1();
    text-align: center;
  }

  .swiper-slide {
    position: relative;

    & > a,img {
      width: 100%;
      display: block;
      height: auto;
      text-decoration: none;
    }

    .slide__content {
      text-align: center;
      h4 {
        @include product-title();
        margin-top: 11px;
      }
      p {
        @include product-category();
      }
      p:last-child {
        @include product-price();
      }
    }
  }

  .swiper-button-next, .swiper-button-prev {
    width: 6px;
    height: 12px;
    color: $black;
  }
}
