.home__collection {
  background-color: $white;
  display: flex;
  flex-flow: row wrap;
  margin: 0 -5px;
  padding: 15px 5.333333333333334% 12px;
  position: relative;
  width: 100%;

  @include break-min($medium1) {
    padding: 40px 3.3333333333333335% 36px;
    margin: 0 -10px;
  }

  .collection__card {
    display: flex;
    flex-direction: column;
    flex: 1 1 48.656716417910445%;
    padding: 5px;
    position: relative;

    @include break-min($medium1) {
      flex: 1 1 32.36607142857143%;
      padding: 10px;
    }

    & > picture > img {
      display: block;
      width: 100%;
    }

    & > span {
      @include h2($white);
      background-color: $green;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 42px;
      text-align: center;
      width: 100%;

      @include break-min($medium1) {
        height: 114px;
      }
    }
  }
}
