.home__hero {
  background-color: $off-white;
  display: flex;
  flex-direction: column;
  position: relative;
  text-align: center;
  width: 100%;

  picture {
    img {
      display: block;
      width: 100%;
    }
  }

  .hero__content {
    padding: 16px 13.333333333333334% 34px;

    @include break-min($large1) {
      padding: 0;
      position: absolute;
      right: 3.75%;
      text-align: left;
      top: 52px;
    }
  }

  h2 {
    @include h1($green);
    @include break-min($large1) {
      color: $white;
    }
  }

  p {
    @include p1($green);
    @include break-min($large1) {
      color: $white;
    }
  }

  button {
    margin: 20px auto 0;
    @include break-min($large1) {
      &.btn {
        background-color: $green;
        border-color: $green;
        color: $white;
      }
    }
  }
}
