.instagram {
  background-color: $white;
  padding: 19px 5.333333333333334% 26px;
  position: relative;
  text-align: center;

  h2 {
    @include h1();
  }

  ul {
     display: flex;
     overflow-x: scroll;

     &::-webkit-scrollbar {
       display: none;
     }

    li {
      flex: 1 0 104px;
      position: relative;
      margin: 0 5px;

      img {
        width: 100%;
      }
    }
  }
}
