.menu__container {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
  height: calc(100vh - 46px);
  left: 0;
  top: 46px;
  width: 100%;
  z-index: $z9;
  visibility: hidden;

  @include break-min($medium1) {
    height: calc(100vh - 58px);
    top: 58px;
  }

  &--show {
    visibility: visible;

    @include break-min($large1) {
      visibility: hidden;
    }
  }

  .menu {
    position: absolute;
    background-color: $white;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    left: 0;
    top: 0;
    width: 84.53333333333333%;
    z-index: $zMax;

      .menu__header {
        position: relative;
        min-height: 50px;

        span.close {
          position: absolute;
          right: 11px;
          top: 11px;
          cursor: pointer;

          svg {
            height: 28px;
            width: 28px;
          }
        }
      }

      .menu__body {
        height: 100%;
        width: 100%;

        ul {
          margin: 0;
          border-bottom:  0.5px solid #F1F1EF;
          &:first-child {
            border-top:  0.5px solid #F1F1EF;
          }

          & > ul {
            border-bottom: 0px solid transparent;
          }

          li {
            @include menu-title();
            padding-left: 11.041009463722396%;
            padding-right: 5.047318611987381%;

            &.state--arrow {
              display: flex;
              align-items: center;
              justify-content: space-between;

              & ~ ul {
                display: none;
              }

              // rotate close arrow
              & > .icon--close {
                transform: rotate(180deg);
                margin-bottom: -10px;
              }

              //display no arrows
              & > .icon {
                display: none;
              }

              //display up arrow on open state
              &.state--open {

                & ~ ul {
                  display: block;
                }

                > .icon--open {
                  display: block;
                }

                > .icon--close {
                  display: none;
                }
              }

              //display down arrown on close state
              & > .icon--close {
                display: block;
              }
            }

            a {
              @include menu-links();
            }
          }
        }
      }

      .menu__footer {
        align-items: center;
        background-color: $off-white;
        display: flex;
        flex-direction: row;
        justify-content: center;
        padding: 24px 1.5772870662460567% 22px 11.041009463722396%;
        width: 100%;

        .img__container {
          flex: 1 1 129px;
          position: relative;
          margin-right: 18px;
          &:before {
            display: block;
            content: "";
            width: 100%;
            padding-top: (1 / 1) * 100%;
          }
          > img {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            width: 100%;
          }
        }

        ul {
          display: flex;
          flex: 1 1 auto;
          flex-direction: column;
        }

        ul > li > a {
          @include menu-links();
        }
      }
    }
}
