/*------ Typography
----------------------------*/

//font smoothing
@mixin smoothing {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*------ Font families
----------------------------*/

//Lato
@mixin lato($weight: 'regular') {
  @include smoothing();
  font-family: 'Lato', sans-serif;
  font-style: normal;

  @if ($weight == 'bold') {
    font-weight: 700;
  } @else if ($weight == 'light') {
    font-weight: 300;
  } @else {
    font-weight: 400;
  }
}

//Pacifico
@mixin pacifico($weight: 'regular') {
  @include smoothing();
  font-family: 'Pacifico', cursive;
  font-weight: 400;
  font-style: normal;
}

/*------ Font styles
----------------------------*/
@mixin promo-text($color: $black) {
  @include lato();
  color: $color;
  font-size: rem(14px);
  font-weight: 500;
  letter-spacing: 0.1em;
  line-height: lh(17px, 14px);
}

@mixin main-nav($color: $black) {
  @include lato();
  color: $color;
  font-weight: 500;
  font-size: rem(20px);
  line-height: lh(24px, 20px);
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

@mixin footer-title($color: $black) {
  @include pacifico();
  color: $color;
  font-size: rem(22px);
  letter-spacing: normal;
  line-height: lh(39px, 22px);
}

@mixin footer-link($color: $black) {
  @include lato();
  color: $color;
  font-size: rem(12px);
  letter-spacing: normal;
  line-height: lh(30px, 12px);
}

@mixin button-text($color: $black) {
  @include lato('bold');
  color: $color;
  font-size: rem(12px);
  line-height: lh(14px, 12px);
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;

  @include break-min($medium1) {
    font-size: rem(14px);
    line-height: lh(17px, 14px);
  }
}

@mixin p1($color: $black) {
  @include lato();
  color: $color;
  font-size: rem(14px);
  line-height: lh(17px, 14px);

  @include break-min($medium1) {
    font-size: rem(22px);
    line-height: lh(26px, 22px);
  }
}

@mixin h1($color: $black) {
  @include pacifico();
  color: $color;
  font-size: rem(26px);
  line-height: lh(46px, 26px);

  @include break-min($medium1) {
    font-size: rem(60px);
    line-height: lh(105px, 60px);
  }
}

@mixin h2($color: $black) {
  @include pacifico();
  color: $color;
  font-size: rem(20px);
  line-height: lh(35px, 20px);

  @include break-min($medium1) {
    font-size: rem(40px);
    line-height: lh(70px, 40px);
  }
}

@mixin product-title($color: $black) {
  @include lato();
  color: $color;
  font-size: rem(16px);
  line-height: lh(19px, 16px);

  @include break-min($medium1) {
    font-size: rem(22px);
    line-height: lh(26px, 22px);
  }
}

@mixin product-category($color: $black) {
  @include lato('light');
  color: $color;
  font-size: rem(12px);
  line-height: lh(14px, 12px);
  text-transform: uppercase;
}

@mixin product-price($color: $green) {
  @include pacifico();
  color: $color;
  font-size: rem(18px);
  line-height: lh(32px, 18px);

  @include break-min($medium1) {
    font-size: rem(26px);
    line-height: lh(46px, 26px);
  }
}

@mixin menu-title($color: $black) {
  @include lato('light');
  color: $color;
  font-size: rem(16px);
  line-height: lh(45px, 16px);
  text-transform: uppercase;
  letter-spacing: normal;

  @include break-min($large1) {
    line-height: lh(19px, 16px);
  }
}


@mixin menu-links($color: $black) {
  @include lato();
  color: $color;
  font-size: rem(18px);
  line-height: lh(38px, 18px);
  text-transform: capitalize;
  letter-spacing: normal;

  @include break-min($large1) {
    font-size: rem(22px);
    line-height: lh(40px, 22px);
  }
}
